.account {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #E0E6ED;
    background-color: #fff;
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    padding: 1.5rem;
    box-sizing: border-box;
    text-align: left;
    margin-bottom: 2rem;
    box-shadow: 2px 2px 5px #000;
}

.account-amount {
    margin: 0;
    font-size: 2.5rem;
    font-weight: bold;
}

.account-amount-description {
    margin: 0;
}

.account-title {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: normal;
}

.account-content-wrapper {
    width: 100%;
    flex: 1;
}

.edit-button {
    border-color: #00BC77;
    background-color: #00BC77;
    color: #fff;
    font-weight: bold;
    padding: 10px;
}

.header {
    color: #fff;
    margin-bottom: 2rem;
}

.transaction-button {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    border-color: #00BC77;
    background-color: #00BC77;
    color: #fff;
}

@media (min-width: 1024px) {
    .account {
        flex-direction: row;
    }

    .account-content-wrapper.cta {
        flex: 0;
    }

    .transaction-button {
        width: 200px;
    }
}


.error-text {
    margin-top: 10%;
    font-size: 3vw;
    font-weight: 600;
}

.error-image {
    width: 60%;
    max-width: 840px;
}

.profile {
    position: absolute;
    top: -100%;
    display: flex;
    flex-direction: column;
    width: 100%;
    flex: 1;
    justify-content: center;
    align-items: center;
    background-color: #E0E6ED;
    padding: 30px;
    color: #000;
    opacity: 0;
    transition: .5s ease-out;
    border-bottom: 3px solid #fff;
    box-shadow: 0 10px 5px rgba(0,0,0,.4);

    h1 {
        font-size: 2rem;
        color: #000;
        margin-bottom: 0;
    }

    p {
        line-height: 0;
        color: #002b1b;
    }

    label {
        margin-top: 15px;
    }

    .profile-wrapper {
        width: 300px;
    }

    .profile-form-save-btn,
    .profile-form-close-btn {
        display: block;
        //width: 100%;
        padding: 8px;
        font-size: 1.1rem;
        font-weight: bold;
        margin-top: 1rem;
        border-color: #6458F5;
        background-color: white;
        color: #6458F5;
        cursor: pointer;
        border-radius: 5px;
}

    .profile-form-close-btn {
        position: absolute;
        top: 0;
        right: 0;
        margin-right: 1.7rem;
        border-radius: 5px;
    }
}